import { Injectable } from '@angular/core'

@Injectable({
    providedIn: 'root',
})
export class CopierService {
    private fakeElem: HTMLTextAreaElement | null

    public copyText(text: string): boolean {
        try {
            this.createFake(text)
            return document.execCommand('copy')
        } catch {
            return false
        } finally {
            this.removeFake()
        }
    }

    /**
     * Creates a fake textarea element, sets its value from `text` property,
     * and makes a selection on it.
     */
    private createFake(text: string): void {
        // Create a fake element to hold the contents to copy
        this.fakeElem = document.createElement('textarea')
        // Prevent zooming on iOS
        this.fakeElem.style.fontSize = '12pt'
        // Reset box model
        this.fakeElem.style.border = '0'
        this.fakeElem.style.padding = '0'
        this.fakeElem.style.margin = '0'
        // Move element out of screen horizontally
        this.fakeElem.style.position = 'absolute'
        this.fakeElem.style.left = '-9999px'
        // Move element to the same position vertically
        const yPosition = window.pageYOffset || (document.documentElement && document.documentElement.scrollTop) || 0
        this.fakeElem.style.top = yPosition + 'px'
        this.fakeElem.setAttribute('readonly', '')
        this.fakeElem.value = text
        document.body.appendChild(this.fakeElem)
        this.fakeElem.select()
        this.fakeElem.setSelectionRange(0, this.fakeElem.value.length)
    }

    private removeFake(): void {
        if (this.fakeElem) {
            document.body.removeChild(this.fakeElem)
            this.fakeElem = null
        }
    }
}
