import { Directive, Input, OnChanges, SimpleChanges } from '@angular/core'
import { AbstractControl, NG_VALIDATORS, ValidationErrors, Validator, Validators } from '@angular/forms'

@Directive({
    selector: '[max][formControlName],[max][formControl],[max][ngModel]',
    providers: [
        {
            provide: NG_VALIDATORS,
            useExisting: MaxValidatorDirective,
            multi: true,
        },
    ],
})
export class MaxValidatorDirective implements Validator, OnChanges {
    @Input()
    public max: number
    private onChanges: () => void

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.max) {
            this.max = changes.max.currentValue
            if (this.onChanges) {
                this.onChanges()
            }
        }
    }

    public validate(control: AbstractControl): ValidationErrors | null {
        if (this.max === undefined || this.max === null) {
            return null
        }
        const required = Validators.required(control)
        if (required !== null) {
            return null
        }
        const v = +control.value
        return v <= +this.max ? null : { actualValue: v, requiredValue: +this.max, max: true }
    }

    public registerOnValidatorChange(fn: () => void): void {
        this.onChanges = fn
    }
}
