import { Pipe, PipeTransform } from '@angular/core'
import BigNumber from 'bignumber.js'

@Pipe({ name: 'pluralize' })
export class PluralizePipe implements PipeTransform {
    public transform(text: string, value: string, otherValue: number = 1): string | null | undefined {
        return new BigNumber(value || 0).isGreaterThan(otherValue) ? text + (text.endsWith('s') ? 'es' : 's') : text
    }
}
