import { Pipe, PipeTransform } from '@angular/core'

@Pipe({ name: 'masked' })
export class MaskedPipe implements PipeTransform {
    public transform(value: string | null | undefined, show = 3, fill = 6): string {
        if (!value) {
            return value ? value : ''
        }
        return value.substr(0, show).padEnd(show + fill, '*')
    }
}
