import { Component, EventEmitter, Input, Output } from '@angular/core'
import { Permission } from '../api-interfaces'
import { permissionMap } from '../common.mixin'
import { SessionService } from '../services/session.service'

@Component({
    selector: 'permission-select',
    templateUrl: 'permission-select.component.html',
})
export class PermissionSelectComponent {
    @Input()
    public permissions: Permission[] = []
    @Output()
    public readonly permissionsChange = new EventEmitter<Permission[]>()

    public paymentPermissions: Permission[] = ['payment-swift', 'payment-local', 'payment-crypto']
    public featurePermissions: Permission[] = [
        'cards',
        'conversions',
        'invoices',
        'trades',
        'spot-trades',
        'marketplaces',
        'redeem',
    ]

    constructor(public session: SessionService) {}

    public togglePaymentMethod(method: Permission, checked: boolean): void {
        if (checked) {
            this.permissions.push(method)
        } else {
            this.permissions = this.permissions.filter(paymentMethod => paymentMethod !== method)
        }
        this.permissionsChange.emit(this.permissions)
    }

    public toggleFeature(feature: Permission, checked: boolean): void {
        if (checked) {
            this.permissions.push(feature)
        } else {
            this.permissions = this.permissions.filter(paymentMethod => paymentMethod !== feature)
        }
        this.permissionsChange.emit(this.permissions)
    }

    public getPermissionLabel(permission: Permission): string {
        return permissionMap[permission]
    }
}
