import { Component, Input } from '@angular/core'
import { TranslateService } from '@ngx-translate/core'
import { IUser } from '../api-interfaces'
import { UserService } from '../services/core/user.service'
import { ToastrService } from '../services/toastr.service'

@Component({
    selector: 'confirm-mail-reminder',
    templateUrl: 'confirm-mail-reminder.component.html',
})
export class ConfirmMailReminderComponent {
    @Input()
    public user: IUser
    public resendTriggered = false
    constructor(private toastr: ToastrService, private userService: UserService, public translate: TranslateService) {}

    public resend(): void {
        this.resendTriggered = true
        this.userService.resendActivationMail(this.user.id).subscribe(
            () => {
                this.toastr.success(this.translate.instant('common.mail-confirmation-sent-again'))
            },
            () => {
                this.resendTriggered = false
            }
        )
    }
}
