import { Component, Input } from '@angular/core'
import { Router } from '@angular/router'
import { environment } from 'src/environments/environment'
import { IUser } from '../api-interfaces'

@Component({
    selector: 'user-display',
    templateUrl: 'user.component.html',
})
export class UserComponent {
    public appName = environment.appName
    public displayName = environment.displayName
    @Input()
    public user: IUser
    @Input()
    public displayAccountNumber = false
    @Input()
    public displayBadges = true
    @Input()
    public disableLink = false
    @Input()
    public maxCharacters = 30

    constructor(private router: Router) {}

    public handleClick(event: MouseEvent, userId: string): void {
        event.preventDefault()

        if (!this.disableLink) {
            this.router.navigate(['/admin/accounts/users', userId])
        }
    }
}
